.sidenav {
  height: 100%;
  width: 200px;
  position: fixed;
  z-index: 0;
  top: 0;
  left: 0;
  background-color: white;
  overflow-x: hidden;
  text-align: left;
  padding-left: 0px;
  padding-top: 100px;
  line-height: 25px;

}
.sidenav a {
  padding: 6px 10px 6px 0px;
  text-decoration: none;
  font-size: 14px;
  color: #818181;
  display: block;

}
.sidenav a:hover {
  color: black;
  transition: color 100ms ease-in;
  text-decoration: none !important;
}

.sidemain {
  margin-left: 160px; /* Same as the width of the sidenav */
  font-size: 28px; /* Increased text to enable scrolling */
  padding-left: 40px;
}

@media screen and (min-width: 990px) {
  .sidemain {
    padding-top: 5px;
  }
}
/*For small screens, hide the navigation and offset the page downwards*/

@media screen and (max-width: 990px) {
  .sidenav {display: none;}
  .sidemain {
    margin-left: 0px;
    padding-left: 0px;
    margin-top: 30px;
  }

}


