/*work page folio item animation*/
.FolioItem {
  opacity:0.9;
  transition: opacity 300ms ease-in;
  border-radius: 3px;
  overflow: hidden;

}
.FolioItem:hover {
  opacity:1;
  transition: opacity 300ms ease-in;

}

.FolioImageClass {
  object-fit: cover;
  object-position: 50% 50%;
}

@media screen and (min-width: 900px) and (max-width: 1200px) {
  .FolioImageClass {
    height: 300px;
  }
}
