/*back button for pages without contents*/
.BackButtonClass {
  position: fixed;
  margin-top: -45px;
  margin-left: 110px;
  z-index: 2;
  animation: fadeInAnimation 2s;
  display: none;
}

/*back button animation 20% is delay to account for page load*/
@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    20% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
}


/*back button for pages with contents*/
.BackButton2Class {
  position: fixed;
  margin-top: 24px;
  margin-left: 110px;
  z-index: 1;
  animation: fadeInAnimation 2s;
  display: none;
}

/*offset backbutton on small screens*/
@media screen and (max-width: 990px) {
  .BackButtonClass {
    margin-top: -34px;
  }
  .BackButton2Class {
    margin-top: 4px;
    position: fixed;

  }
}


